import service from "@/utils/http"

export const get_poc = (data) => {
    const refactor_data = {page_num: 1, page_size: 1}
    refactor_data.page_num = data.page_num
    refactor_data.page_size = data.page_size
    return service.get(`/poc/${refactor_data.page_num}/${refactor_data.page_size}`)
}

export const sync_poc = () => {
    return service.get('/poc/sync')
}

export const get_tree_poc = (data) => {
    const refactor_data = {query: ''}
    if(data.query == ''){
        refactor_data.query = 'null'
    }else{
        refactor_data.query = data.query
    }
    return service.get(`/poc/treepoc/${refactor_data.query}`)
}